<template>
  <div class="demo">
    <div class="content">

      <div class="block" :class="loaded >= i?'green':'blue'" v-intersection.once="handler" :data-index="i" v-for="i in 3" :key="i">
        <span class="loadMsg">{{loaded >= i? finished:notLoad }}</span>
        <span ref="loading"></span>
      </div>

    </div>
    <span class="tip button blue">loaded data:{{loaded}}</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: "???",
      loaded: 0,
      start: "begin to load",
      finished: "finished",
      notLoad: "not load",
    };
  },
  methods: {
    handler(entries) {
      let index = entries[0].target.dataset.index;

      if (entries[0].isIntersecting) {

        this.begin2load(index, true);
        console.log("    this.$refs.loading: ", this.$refs.loading);
        setTimeout(() => {
          this.loaded++;
          this.begin2load(index, false);
        }, 2000);
      } else {
        return;
      }
    },

    begin2load(index, flag) {
      if (flag) {
        
        this.$toast.info(`data${index} block is loading now`);
        this.$refs.loading[index - 1].className = "lds-hourglass";
      } else {
        
        this.$refs.loading[index - 1].className = "";
       
        this.$toast.success(`data${index} block is finished`);

      }
    },
  },
};
</script>

<style lang="scss" scoped>

.block{
  margin:15vh  auto;
}
.loadMsg {
  color: #ffebd1;
  font-size: 16px;
  font-weight: 600;
}
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}
</style>

