export default "<template>\n  <div class=\"demo\">\n\n    <div class=\"content\">\n      <div class=\"block blue\" v-intersection=\"handler\"></div>\n      <div class=\"block\"></div>\n    </div>\n\n    <span class=\"tip button1\" :class=\"isShow ? 'green' : 'red'\">isShow?:{{ isShow }} times:{{ times }}</span>\n  \n  \n\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      isShow: \"???\",\n      times: 0,\n    };\n  },\n  computed: {},\n  methods: {\n\n    handler(entries, ob) {\n      console.log(\"ob: \", ob);\n      console.log(\"entries: \", entries);\n      entries[0].isIntersecting ? (this.isShow = true) : (this.isShow = false);\n      this.times++;\n      this.notify();\n    },\n    notify() {\n      this.$toast.clear();\n      this.isShow\n        ? this.$toast.success(\"demo1 block is show\")\n        : this.$toast.error(\"demo1 block not show\");\n      console.log(\"this.$toast: \", [this.$toast]);\n    },\n  },\n};\n</script>\n\n<style lang=\"css\">\n.demo {\n  position: relative;\n  border-radius: 20px;\n\n  border: 1px solid #cccccc;\n  overflow: hidden;\n}\n.content {\n  width: 100%;\n  height: 30vh;\n  overflow-y: scroll;\n}\n\n.block {\n  width: 10vh;\n  height: 10vh;\n  margin: 40vh auto;\n  margin-bottom: 20%;\n\n  border-radius: 20px;\n}\n</style>\n";