export default "<template>\n  <div class=\"demo\">\n    <div class=\"content\">\n      <ul v-if=\"range\">\n        <li class=\"block blue\" v-for=\"index in range\" :data-index=\"index\" :key=\"index\" v-intersection=\"yourFn\">\n          {{ index }}\n        </li>\n      </ul>\n      <transition-group v-if=\"navlist\" name=\"in-view\" class=\"Pagination\" tag=\"ul\">\n        <li v-for=\"item in navlist\" :key=\"item\" class=\"in-view-item\">\n          {{ item }}\n        </li>\n      </transition-group>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      range: Array.from({ length: 10 }, (_, i) => i + 1),\n      ishowList: {},\n    };\n  },\n  computed: {\n    navlist() {\n      return Object.entries(this.ishowList)\n        .sort((a, b) => Number(a) - Number(b))\n        .filter(([, e]) => e)\n        .map(([id]) => id);\n    },\n  },\n\n  methods: {\n    yourFn(entries, observer, isIntersecting) {\n      let id = entries[0].target.dataset.index;\n\n      this.$set(this.ishowList, id, isIntersecting);\n    },\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.block {\n  margin: 1vh;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  color: #ffebd1;\n  font-weight: 600;\n}\n.demo {\n  position: relative;\n}\n.Pagination {\n  position: absolute;\n  right: 30px;\n  top: 50%;\n\n  transform: translateY(-50%);\n\n  li {\n    width: 20px;\n    height: 20px;\n    border-color: rgba(170, 106, 175, 0.8);\n    background-color: rgba(133, 155, 179, 1);\n    color: #ffebd1;\n    font-weight: 600;\n    border-radius: 4px;\n    margin: 5px 0;\n  }\n}\n\n.in-view-item {\n  transition: all 0.75s;\n  display: block;\n}\n.in-view-enter {\n  opacity: 0;\n  transform: translateX(30px);\n}\n.in-view-leave-to {\n  opacity: 0;\n  transform: translateX(30px);\n}\n.in-view-leave-active {\n  position: absolute;\n}\n</style>\n";