<template>
  <div class="demo">

    <div class="content">
      <div class="block blue" v-intersection="handler"></div>
      <div class="block"></div>
    </div>

    <span class="tip button1" :class="isShow ? 'green' : 'red'">isShow?:{{ isShow }} times:{{ times }}</span>
  
  

  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: "???",
      times: 0,
    };
  },
  computed: {},
  methods: {

    handler(entries, ob) {
      console.log("ob: ", ob);
      console.log("entries: ", entries);
      entries[0].isIntersecting ? (this.isShow = true) : (this.isShow = false);
      this.times++;
      this.notify();
    },
    notify() {
      this.$toast.clear();
      this.isShow
        ? this.$toast.success("demo1 block is show")
        : this.$toast.error("demo1 block not show");
      console.log("this.$toast: ", [this.$toast]);
    },
  },
};
</script>

<style lang="css">
.demo {
  position: relative;
  border-radius: 20px;

  border: 1px solid #cccccc;
  overflow: hidden;
}
.content {
  width: 100%;
  height: 30vh;
  overflow-y: scroll;
}

.block {
  width: 10vh;
  height: 10vh;
  margin: 40vh auto;
  margin-bottom: 20%;

  border-radius: 20px;
}
</style>
