export default "<template>\n  <div class=\"demo\">\n    <div class=\"content\">\n      <div class=\"block blue\" v-intersection.quiet=\"handler\" :key=\"3\"></div>\n      <div class=\"block\"></div>\n    </div>\n    <span class=\"tip button\" :class=\"color\"\n      >isShow?:{{ isShow }}  times:{{times}}\n    </span>\n  </div>\n</template>\n\n<script>\nexport default {\n\n  data() {\n    return {\n      isShow: \"not handle yet , i don't know\",\n        times:0\n    };\n  },\n\n  computed: {\n     color(){\n     if( this.isShow === true ){\n       return 'green'\n     }else if(this.isShow === false){\n        return 'red'\n     }else{\n       return 'orange'\n     }\n    }\n  },\n  methods: {\n    handler(entries) {\n      entries[0].isIntersecting ? (this.isShow = true) : (this.isShow = false);\n      this.times++\n    },\n    notify(){\n      this.isShow?this.$toast.success(\"demo1 block is show\"):this.$toast.error(\"demo1 block not show\")\n    }\n  },\n};\n</script>\n";